<template>
    <div class="container">
      <div class="card card-primary">
        <div class="card-header">
          <h3 class="card-title">Добавити кінотеатр</h3>
        </div>
        <div>
          <div class="card-body">
            <div class="form-group">
              <label for="nameCinema">Назва кінотеатра</label>
              <input
                type="text"
                class="form-control"
                id="nameCinema"
                placeholder="Введіть назву кінотеатра"
                v-model="nameCinema"
                required
              />
            </div>
            <div class="form-group">
              <label for="description">Опис</label>
              <textarea
                type="text"
                class="form-control"
                id="description"
                placeholder="Текст"
                v-model="description"
              ></textarea>
            </div>
            <div class="form-group">
              <label for="conditions">Умова</label>
              <textarea
                type="text"
                class="form-control"
                id="conditions"
                placeholder="Текст"
                v-model="conditions"
              ></textarea>
            </div>
            <div class="file-input">
                <label for="holovnaImg">Логотип</label>
                <input type="file" ref="cinemaLogo" class="file-input__input" @change="changeFileCinema" v-if="cinemaLogo === null">
                <button @click="triggerCinemaLogo" class="btn btn-block btn-default"  v-if="cinemaLogo === null">Добавити</button>
                <div class="image-delete_btn">
                  <div class="image-delete">
                    <img :src="cinemaLogo" class="imgInput" v-if="cinemaLogo">
                  </div>
                  <button @click="deleteCinemaLogo" class="btn btn-block btn-default"  v-if="cinemaLogo !== null">Видалити</button>
                </div>
            </div>
            <div class="file-input">
                <label for="holovnaImg">Фото верхнього банера</label>
                <input type="file" ref="bannerFoto" class="file-input__input" @change="changeFileBanner" v-if="bannerFoto === null">
                <button @click="triggerBannerFoto" class="btn btn-block btn-default"  v-if="bannerFoto === null">Добавити</button>
                <div class="image-delete_btn">
                  <div class="image-delete">
                    <img :src="bannerFoto" class="imgInput" v-if="bannerFoto">
                  </div>
                  <button @click="deleteBannerFoto" class="btn btn-block btn-default"  v-if="bannerFoto !== null">Видалити</button>
                </div>
            </div>
            <div class="file-input">
                <label for="holovnaImg">Галерея картинок</label>
                <input type="file" ref="fileInput2" class="file-input__input" @change="handleFileChange2" multiple>
                <button @click="triggerFileInput2" class="btn btn-block btn-default" v-if="galleryImgs == []">Добавити</button>
                <div class="image-delete_btn2">
                  <div class="image-delete" v-for="(galleryImg, i) in galleryImgs" :key="i">
                    <img :src="galleryImg" class="imgInput" v-if="galleryImg">
                    <span class="deleteSpan" v-if="galleryImg" @click="deleteGalleryImg(i)">x</span>
                  </div>
                  <button @click="triggerFileInput2" class="btn btn-block btn-default"  v-if="galleryImgs != []">Добавити</button>
                </div>
            </div>
            <div class="row">
              <div class="col-12">
              <div class="card">
              <div class="card-header">
                <h3 class="card-title">Список залів</h3>
              </div>

              <div class="card-body table-responsive p-0">
              <table class="table table-hover text-nowrap">
              <thead>
              <tr>
              <th>Назва</th>
              <th>Дата створення</th>
              <th></th>
              <th></th>
              </tr>
              </thead>
              <tbody>
              <tr v-for="(hall, i) in halls" :key="i">
              <td>{{ hall.name }}</td>
              <td>{{ hall.data }}</td>
              <td>
                <router-link to='/'>
                  <img src="@/assets/img/redactor.png" alt="">
                </router-link>
              </td>
              <td @click="deleteHall">
                <img src="@/assets/img/delete.png" alt="">
              </td>
              </tr>
              </tbody>
              </table>
              </div>

              </div>

              </div>
              <button @click="addHall" class="btn btn-block btn-default">Створити зал</button>
            </div>
            <div class="modal-hall" v-if="modalHall === true" @click.self="closeHall">
              <div class="card card-primary modal-width" @click.stop>
                <div class="card-header">
                  <h3 class="card-title">Добавити зал</h3>
                  <div class="close-hall" @click="closeHall">
                    <img src="@/assets/img/close.png" alt="">
                  </div>
                </div>
                <form @submit.prevent="saveHall">
                  <div class="card-body">
                    <div class="form-group">
                      <label for="nameHall">Назва зала</label>
                      <input
                        type="text"
                        class="form-control"
                        id="nameHall"
                        placeholder="Введіть назву зала"
                        v-model="nameHall"
                        required
                      />
                    </div>
                    <div class="form-group">
                      <label for="dataHall">Дата створення</label>
                      <input
                        type="date"
                        class="form-date"
                        id="dataHall"
                        v-model="dateHall"
                        required
                      />
                    </div>
                    <div class="form-group">
                      <label for="descriptionHall">Опис</label>
                      <textarea
                        type="text"
                        class="form-control"
                        id="descriptionHall"
                        placeholder="Текст"
                        v-model="descriptionHall"
                      ></textarea>
                    </div>
                    <div class="file-input">
                        <label for="holovnaImg">Схема зала</label>
                        <input type="file" ref="schemeHall" class="file-input__input" @change="changeFileHall" v-if="schemeHall === null">
                        <button @click="triggerSchemeHall" class="btn btn-block btn-default"  v-if="schemeHall === null">Добавити</button>
                        <div class="image-delete_btn">
                          <div class="image-delete">
                            <img :src="schemeHall" class="imgInput" v-if="schemeHall">
                          </div>
                          <button @click="deleteSchemeHall" class="btn btn-block btn-default"  v-if="schemeHall !== null">Видалити</button>
                        </div>
                    </div>
                    <div class="file-input">
                        <label for="holovnaImg">Верхній банер</label>
                        <input type="file" ref="bannerFoto2" class="file-input__input" @change="changeFileBanner2" v-if="bannerFoto2 === null">
                        <button @click="triggerBannerFoto2" class="btn btn-block btn-default"  v-if="bannerFoto2 === null">Добавити</button>
                        <div class="image-delete_btn">
                          <div class="image-delete">
                            <img :src="bannerFoto2" class="imgInput" v-if="bannerFoto2">
                          </div>
                          <button @click="deleteBannerFoto2" class="btn btn-block btn-default"  v-if="bannerFoto2 !== null">Видалити</button>
                        </div>
                    </div>
                    <div class="file-input">
                        <label for="holovnaImg">Галерея картинок</label>
                        <input type="file" ref="fileHall" class="file-input__input" @change="handleFileHall" multiple>
                        <button @click="triggerFileHall" class="btn btn-block btn-default" v-if="galleryImgs2 == []">Добавити</button>
                        <div class="image-delete_btn2">
                          <div class="image-delete" v-for="(galleryImg, i) in galleryImgs2" :key="i">
                            <img :src="galleryImg" class="imgInput" v-if="galleryImg">
                            <span class="deleteSpan" v-if="galleryImg" @click="deleteGalleryImg2(i)">x</span>
                          </div>
                          <button @click="triggerFileHall" class="btn btn-block btn-default"  v-if="galleryImgs != []">Добавити</button>
                        </div>
                    </div>
                    
                    <div class="form-group">
                      <h5>SEO блок:</h5>
                      <label>URL</label>
                      <input
                        type="text"
                        class="form-control"
                        placeholder="URL"
                        v-model="urlHall"
                      />
                    </div>
                    <div class="form-group">
                      <label>Title</label>
                      <input
                        type="text"
                        class="form-control"
                        placeholder="Title"
                        v-model="titleHall"
                      />
                    </div>
                    <div class="form-group">
                      <label>Keywords</label>
                      <input
                        type="text"
                        class="form-control"
                        placeholder="word"
                        v-model="keywordsHall"
                      />
                    </div>
                    <div class="form-group">
                      <label>Description</label>
                      <textarea
                        type="text"
                        class="form-control"
                        placeholder="Description"
                        v-model="descriptionSeoHall"
                      >
                      </textarea>
                    </div>
                  </div>  
                  <div class="card-footer">
                    <button @click="saveHall" class="btn btn-primary">Зберегти</button>
                  </div>
                </form>
              </div>
            </div>
            <div class="form-group">
              <h5>SEO блок:</h5>
              <label>URL</label>
              <input
                type="text"
                class="form-control"
                placeholder="URL"
                v-model="url"
              />
            </div>
            <div class="form-group">
              <label>Title</label>
              <input
                type="text"
                class="form-control"
                placeholder="Title"
                v-model="title"
              />
            </div>
            <div class="form-group">
              <label>Keywords</label>
              <input
                type="text"
                class="form-control"
                placeholder="word"
                v-model="keywords"
              />
            </div>
            <div class="form-group">
              <label>Description</label>
              <textarea
                type="text"
                class="form-control"
                placeholder="Description"
                v-model="descriptionSeo"
              >
              </textarea>
            </div>
          </div>  
          <div class="card-footer">
            <button @click="saveCinema" class="btn btn-primary">Зберегти</button>
          </div>
        </div>
      </div>
    </div>
  </template>
  <script>
  
  //import { ref } from 'vue';
  //import { storage } from '@/firebase/index.js'
  import { getDatabase, ref as dbRef, set, push, update, get, onValue} from 'firebase/database'
  import { getStorage, ref as storRef, uploadBytes, put, getDownloadURL } from 'firebase/storage'
  
  export default {
    setup() {
     // const imageUrl = ref(null)
  
      // const uploudFile = async (event) => {
      //   const file = event.target.files[0]
      //   const storage = getStorage()
      //   const storageRef = ref(storage, 'images/' + file.name)
      //   //storageRef.put(file)
      //   await uploadBytes(storageRef, file);
      //   console.log(event.target.files[0])
      //   // const fileRef = storageRef.child(file.name)
      //   // await uploadBytes(fileRef, file)
      //   const url = getDownloadURL(storageRef)
      //   console.log(url)
      //   //imageUrl.value = url
      // }
  
      //return { imageUrl, uploudFile }
    },
  
    data() {
      return {
        cinema: {},
        nameCinema: '',
        description: '',
        descriptionHall: '',
        conditions: '',
        cinemaLogo: null,
        schemeHall: null,
        fileCinemaLogo: null,
        fileBannerFoto: null,
        fileBannerFoto2: null,
        fileScheneHall: null,
        bannerFoto: null,
        bannerFoto2: null,
        galleryImgs: [],
        galleryImgs2: [],
        halls: [],
        url: '',
        urlHall: '',
        title: '',
        titleHall: '',
        keywords: '',
        keywordsHall: '',
        descriptionSeo: '',
        descriptionSeoHall: '',
        fileImgSrc: null,
        fileGalleryImg: [],
        fileGalleryImg2: [],
        modalHall: false,
      }
    },
    methods: {
      changeFileCinema(event){
        this.fileCinemaLogo = event.target.files[0];
        this.cinemaLogo = URL.createObjectURL(this.fileCinemaLogo);
      },
      triggerCinemaLogo() {
        this.$refs.cinemaLogo.click();
      },
      changeFileBanner(event){
        this.fileBannerFoto = event.target.files[0];
        this.bannerFoto = URL.createObjectURL(this.fileBannerFoto);
      },
      triggerBannerFoto(){
        this.$refs.bannerFoto.click();
      },
      changeFileHall(event){
        this.fileScheneHall = event.target.files[0];
        this.schemeHall = URL.createObjectURL(this.fileScheneHall);
      },
      triggerSchemeHall(){
        this.$refs.schemeHall.click();
      },

      changeFileBanner2(event){
        this.fileBannerFoto2 = event.target.files[0];
        this.bannerFoto2 = URL.createObjectURL(this.fileBannerFoto2);
      },
      triggerBannerFoto2(){
        this.$refs.bannerFoto2.click();
      },

      handleFileChange2(event) {
        this.fileGalleryImg = [...event.target.files]
        for(let image of this.fileGalleryImg){
          this.imageSrc2 = URL.createObjectURL(image)
          this.galleryImgs.push(this.imageSrc2)
        }
      },

      handleFileHall(event){
        this.fileGalleryImg2 = [...event.target.files]
        for(let image of this.fileGalleryImg2){
          const imageSrc = URL.createObjectURL(image)
          this.galleryImgs2.push(imageSrc)
        }
      },

      triggerFileInput2(){
        this.$refs.fileInput2.click();
      },
      triggerFileHall(){
        this.$refs.fileHall.click();
      },

      deleteCinemaLogo(){
        this.cinemaLogo = null
      },
      deleteBannerFoto(){
        this.bannerFoto = null
      },
      deleteSchemeHall(){
        this.schemeHall = null
      },
      deleteBannerFoto2(){
        this.bannerFoto2 = null
      },
      deleteGalleryImg(i){
        this.galleryImgs.splice(i, 1)
      },
      deleteGalleryImg2(i){
        this.galleryImgs2.splice(i, 1)
      },

      deleteHall(){

      },
      addHall(){
        if(this.nameCinema !== ''){
          this.modalHall = true
          document.documentElement.classList.add('modal-open');
        } 
      },
      closeHall(){
        this.modalHall = false
        document.documentElement.classList.remove('modal-open');
      },

      async saveHall(){
        const db = getDatabase()
        const storage = getStorage()
        // Додайте фільм до бази даних Firebase
        const path = `cinemas/${this.nameCinema}/halls/${this.nameHall}`
        const moviesRef = dbRef(db, path);

        set(moviesRef,  {
          name: this.nameHall,
          date: this.dateHall,
          description: this.descriptionHall,
          schemeHall: this.schemeHall,
          bannerHall: this.bannerFoto2,
          galleryImages: this.galleryImgs2,
          url: this.urlHall,
          title: this.titleHall, 
          keywords: this.keywordsHall,
          descriptionSeo: this.descriptionSeoHall
        })

        if (this.fileScheneHall) {
          const storageRef = storRef(storage, `cinemas/${this.nameCinema}/schemeHall`);
          uploadBytes(storageRef, this.fileScheneHall).then((snapshot) => {
            getDownloadURL(snapshot.ref).then((url) => {
              update(moviesRef, {
                schemeHall: url
              });
            });
          });
        }

        if (this.fileBannerFoto2) {
          const storageRef = storRef(storage, `cinemas/${this.nameCinema}/bannerHall`);
          uploadBytes(storageRef, this.fileBannerFoto2).then((snapshot) => {
            getDownloadURL(snapshot.ref).then((url) => {
              update(moviesRef, {
                bannerHall: url
              });
            });
          });
        }

        // Завантажити картинки галереї до Firebase Storage та зберегти посилання на них до фільму в Firebase Realtime Database
        const snapshot = await get(moviesRef)
        const oldBannerTop = (snapshot.val() && snapshot.val().galleryImages) || []
        const galleryImages = [...oldBannerTop]

        await Promise.all(this.fileGalleryImg2.map( async(galleryImg) => {
        const name = 'image-' + Date.now() + Math.floor(Math.random() * 1000);
          const storageRef = storRef(storage, `cinemas/${this.nameCinema}/galleryImgsHalls/${this.nameHall}/${name}`);
          await uploadBytes(storageRef, galleryImg);
          const url = await getDownloadURL(storageRef);
          galleryImages.push({
            name: name,
            url: url
          });
        }))

        update(moviesRef, {
          galleryImages
        });

        this.closeHall()
      },

      async getHall() {
        const db = getDatabase()
        const filmRef = dbRef(db, 'cinemas/' + this.nameCinema);
        await new Promise((resolve) => {
          onValue(filmRef, (snapshot) => {
            if (snapshot.exists()) {
              this.cinema = snapshot.val();
              if (typeof this.cinema.galleryImages !== 'undefined' && Array.isArray(this.cinema.galleryImages)) {
                this.galleryImages = this.film.galleryImages;
              } else {
                this.galleryImages = [];
              }
              this.halls = this.cinema.halls
            } else {
              console.log('Кінотеатр не знайдено');
            }
            resolve();
          });
        });
      },


      async saveCinema(){
        const db = getDatabase()
        const storage = getStorage()
        // Додайте фільм до бази даних Firebase
        const path = `cinemas/${this.nameCinema}`
        const moviesRef = dbRef(db, path);
        //const newMovieRef = push(moviesRef);
        set(moviesRef, {
          id: moviesRef.key,
          nameCinema: this.nameCinema,
          description: this.description,
          conditions: this.conditions,
          cinemaLogo: this.cinemaLogo,
          bannerFoto: this.bannerFoto,
          halls: this.halls,
          url: this.url,
          title: this.title, 
          keywords: this.keywords,
          descriptionSeo: this.descriptionSeo
        });
  
          // Завантажити головну картинку до Firebase Storage та зберегти посилання на неї до фільму в Firebase Realtime Database
        if (this.fileCinemaLogo) {
          const storageRef = storRef(storage, `cinemas/${moviesRef.key}/cinemaLogo`);
          uploadBytes(storageRef, this.fileCinemaLogo).then((snapshot) => {
            getDownloadURL(snapshot.ref).then((url) => {
              update(moviesRef, {
                cinemaLogo: url
              });
            });
          });
        }

        if (this.fileBannerFoto) {
          const storageRef = storRef(storage, `cinemas/${moviesRef.key}/bannerFoto`);
          uploadBytes(storageRef, this.fileBannerFoto).then((snapshot) => {
            getDownloadURL(snapshot.ref).then((url) => {
              update(moviesRef, {
                bannerFoto: url
              });
            });
          });
        }
  
          // Завантажити картинки галереї до Firebase Storage та зберегти посилання на них до фільму в Firebase Realtime Database
          const snapshot = await get(moviesRef)
          const oldBannerTop = (snapshot.val() && snapshot.val().galleryImages) || []
          const galleryImages = [...oldBannerTop]
  
          await Promise.all(this.fileGalleryImg.map( async(galleryImg) => {
          const name = 'image-' + Date.now() + Math.floor(Math.random() * 1000);
            const storageRef = storRef(storage, `cinemas/${moviesRef.key}/galleryImgs/${name}`);
            await uploadBytes(storageRef, galleryImg);
            const url = await getDownloadURL(storageRef);
            galleryImages.push({
              name: name,
              url: url
            });
          }))
  
          update(moviesRef, {
            galleryImages
          });
  
        this.$router.push('theatres')
  
          // Очистіть поля форми
        this.nameCinema = ''
        this.description = ''
        this.conditions = ''
        this.cinemaLogo = null
        this.bannerFoto = null
        this.galleryImgs = []
        this.halls = []
        this.url = ''
        this.title = ''
        this.keywords = ''
        this.descriptionSeo = ''
      }
    },

    async mounted() {
      await this.getHall();
    },
  
  }
  </script>
  <style lang="scss">
  
  .container{
      width: 70%;
  }
  // input[type="file"] {
  //   display: block;
  //   opacity: 1;  
  // }
  
  .upload-button {
    display: inline-block;
    border: 1px solid #ccc;
    padding: 6px 12px;
    cursor: pointer;
  }
  
  .upload-preview {
    display: inline-block;
    width: 100px;
    height: 100px;
    border: 1px solid #ccc;
    margin-left: 20px;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
  }
  label{
    display: block;
    text-align: left;
  }
  .file-input {
    position: relative;
  }
  .file-input__input {
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
    width: 30%;
    height: 70px;
    cursor: pointer;
  }
  .btn-block {
      display: block;
      width: 30%;
  }
  .imgInput{
    width: 250px;
    height: 250px;
    display: block;
  }
  .image-delete_btn{
    display: flex;
    gap: 40px;
    align-items: end;
    margin-bottom: 0.5rem;
  }
  .image-delete_btn2{
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    align-items: end;
    margin-bottom: 0.5rem;
  }
  .deleteSpan{
    position: absolute;
    top: -8px;
    right: -8px;
    border: 1px solid black;
    background: white;
    border-radius: 50%;
    color: black;
    width: 26px;
    cursor: pointer;
  }
  .image-delete{
    position: relative;
  }
  .form-main{
    display: flex;
    gap: 20px;
  }
  .form-check-input {
    display: block;
  }
  .form-group{
  
    h5{
      text-align: start;
    }
  }
  .card-footer{
    display: flex;
    gap: 30px;
    justify-content: center;
  }

  td{
    img{
      width: 20px;
      cursor: pointer;
    }
  }
  .card-title {
    font-weight: 700;
  }
  .row {
    justify-content: center;
  }
  .modal-hall{
    position: fixed;
    width: 100%;
    top: 0;
    left: 0;
    padding: 100px 0;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 9999;
    overflow: scroll;
  }
  .modal-width{
    width: 60%;
    margin: 0 auto;
  }
  html.modal-open {
    overflow: hidden;
  }
  .form-date{
    display: block;
    width: 20%;
    height: calc(2.25rem + 2px);
    padding: 0.375rem 0.75rem;
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5;
    color: #495057;
    background-color: #fff;
    background-clip: padding-box;
    border: 1px solid #ced4da;
    border-radius: 0.25rem;
    box-shadow: inset 0 0 0 transparent;
    transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
 }

 .close-hall{
  position: absolute;
  right: 1.25rem;
  cursor: pointer;
 }
  </style>
  